body.archive {

    .header_blue,
    .header_blue_light {
      min-width: 450px;
      max-width: 750px;
      box-shadow: 0 3px 45px rgba(0, 0, 0, .15);
    }

    .header_blue_light {
      margin-top: 2px;
      min-height: 5px;
      background-color: #00AEEE;
    }

    .header_blue {
      min-width: 350px;
      background-color: #006daf;
      text-shadow: 0 3px 6px rgba(0, 0, 0, .15);
      p {
        font-size: 18px;
      }
      h1 {
        font-size: 50px;
        line-height: 1.1em;
        font-weight: 700;
      }
    }

    #categpry_header {
      top: 0;
      left: 0;
      z-index: -1;

      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: rgb(255,255,255);
        background: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(255,255,255,0.22032563025210083) 100%);
      }
    }

    &.tax-product_cat {
      .card-category {
        width: 30% !important;
        height: auto !important;
        min-height: 200px;;
        a {
          font-weight: 600 !important;
          font-size: 28px !important;
          line-height: 32px !important;
        }
      }
    }
    .card-category {
      width: 360px;
      height: 360px;
      box-shadow: 0 3px 99px rgba(0, 0, 0, .25);

      &.disabled {
        a:hover {
          span.cat_name {
            opacity: 0;
          }
          span.show_in_hover {
            opacity: 1;
            padding-left: 10%;
            i {
              opacity: 1;
            }
          }
        }
      }

      &:before,
      &:after {
        content: '';
        position: absolute;
        width: 100%;
      }
      &:before {
        top: 10px;
        right: 10px;
        height: 100%;
        background: -moz-radial-gradient(center, ellipse farthest-corner,  rgba(0,0,0,0.15) 0%, rgba(0,0,0,0.1) 50%, rgba(0,0,0,0.15) 100%);
        background: -webkit-radial-gradient(center, ellipse farthest-corner,  rgba(0,0,0,0.15) 0%,rgba(0,0,0,0.1) 50%,rgba(0,0,0,0.15) 100%);
        background: radial-gradient(ellipse at center,  rgba(0,0,0,0.15) 0%,rgba(0,0,0,0.1) 50%,rgba(0,0,0,0.15) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#26000000', endColorstr='#26000000',GradientType=1 );
      }

      &:after {
        z-index: 0;
        opacity: .25;
        bottom: 0;
        left: 0;
        width: calc( 100% - 10px);
        height: calc( 100% - 10px);
        background: -moz-radial-gradient(center, ellipse farthest-corner,  rgba(255,255,255,1) 0%, rgba(255,255,255,0) 100%);
        background: -webkit-radial-gradient(center, ellipse farthest-corner,  rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
        background: radial-gradient(ellipse at center,  rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=1 );

      }

      a {
        z-index: 1;
        font-weight: 700;
        font-size: 38px;
        line-height: 42px;
        text-shadow: 1px 3px 45px rgba(0, 0, 0, .5);
        text-decoration: none !important;
        transition: all .200s ease-in-out;
        span {
          transition: all .250s ease-in-out;
          i {
            transition: all .150s ease-in-out;
          }
        }
        span.cat_name {
          opacity: 1;
        }
        span.show_in_hover {
          width: 90%;
          left: 0;
          opacity: 0;
          i {
            opacity: 0;
            bottom: 5px;
            left: 10px;
            font-size: 16px;
          }
        }
      }

      &:not(.disabled) {
        a {
          &:hover {
            @extend .pb-5;

            + .arrow-go {
              opacity: 1;
            }
          }
        }
      }

      .arrow-go {
        opacity: 0;
        bottom: 10px;
        left: 20px;
        transition: all .250s ease-in-out;
      }
    }

    ul.products {
      @extend .d-flex;
      @extend .flex-wrap;
      @extend .justify-content-between;
      padding-top: 90px !important;
      @extend .px-5;
      @extend .pb-5;
      @extend .shadow-lg;
      background-color: #fff;

      list-style: none;

      li {
        position: relative;
        @extend .mb-6;

        h2 {
          text-align: center;
          text-transform: uppercase;
          @extend .mt-2;
          @extend .small;
          color: #555;
        }

        a.button {
          display: none;
        }
      }

      img {
        &:hover {
            // opacity: 0;
        }
        + .blurred {
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-color: #fff;
            opacity: 0;
            transition: opacity 0.3s ease;

            &:hover {
                opacity: 1 !important;
            }

            img {
                filter: blur(8px);
                -webkit-filter: blur(8px);
                opacity: 0.7;
            }
            span.product_title {
                top: 45%;
                left: 0;
                height: 0;
                span {
                    font-size: 14px;
                    font-weight: 700;
                    color: $primary;
                    text-shadow: 0 0 25px #fff;
                    + span {
                        font-size: 20px;
                        font-weight: 500;
                    }
                }
            }
        }
      }
    }
}



