

body > div.loading {
    @extend .position-fixed;
    z-index: 999;
    top: 0;
    left: 0;
    bottom: 0;
    @extend .w-100;

    transition: opacity 0.35s ease;

    &.opacity-d-none {
        opacity: 0;
    }
}

.page-header {
    margin: 30px 0;
    padding: 30px 0;
    text-align: center;
}


#recent_posts_slider {
    > div {
        overflow: hidden;
        width: 50%;
        @include media-breakpoint-up(md) {
            width: 25%;
        }

        img,
        p {
            transition: all .3s ease-in-out;
        }
        
        &:hover {
            img {
                transform: scale(1.1, 1.1);

                + p {
                    padding-bottom: 10px !important;
                    bottom: 18%;
                }
            }
        }

        p {
            bottom: 10%;
            white-space: nowrap;
            overflow: hidden;
        }
    }
}


